@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.editor {
  font-family: 'jetbrains-mono', monospace;
  counter-reset: line;
  border: 1px solid #ced4da;
}

.editor #code-area {
  outline: none;
  padding-left: 60px !important;
}

.editor pre {
  padding-left: 60px !important;
}

.editor pre .token {
  font-family: 'jetbrains-mono', monospace !important;
}

.editor pre .punctuation {
  color: black;
  font-weight: bold;
}

.editor pre .data-type {
  color: #990055;
  font-weight: bold;
}

.editor .editor-line-number {
  font-family: 'jetbrains-mono', monospace;
  position: absolute;
  left: 0;
  color: #a1a1a1;
  text-align: right;
  width: 40px;
  font-weight: 100;
  border-right: 1px solid #e3e9ef;
  padding-right: 15px;
  background-color: #efefef;
}

code[class*="language-"], pre[class*="language-"] {
  font-family: 'jetbrains-mono', monospace;
}
.errors-list-element {
    color: red;
}
.errors-wrapper {
    margin-top: 1rem;
}

.no-errors {
    display: flex;
    color: darkgreen;
    margin-top: 1rem;
    align-items: center;
}

.no-errors-str {
    margin-left: 1rem;
}

.found-errors {
    display: flex;
    color: darkred;
    align-items: center;
    margin-bottom: 0.5rem;
}

.found-errors-str {
    margin-left: 1rem;
}

.errors-list {
    list-style: none;
    padding: 0;
    margin: 0;
}
* {
  font-family: 'roboto', sans-serif;
}

.App {
  width: 900px;
  margin: auto;
}

#result {
  display: block;
  padding: 0.5rem;
}
